import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/services/http.service';
import { Movies, MovieResponse } from 'src/models/movies';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  catalogueFilterValue: string;
  searchString: string = "";
  filterString: string = "";
  p: number = 1;
  movies: Movies[];
  movieDetail: Movies;
  total: string;
  showDetails: boolean = false;
  hasMovies: string = "";

  constructor(
    private httpService: HttpService
  ) { }

  ngOnInit(): void {

  }

  onChange(): void {
    this.hasMovies = "";
    this.movies = [];
    this.showDetails = false;
    if(this.searchString){
      this.httpService.GetMovieList(this.searchString, this.p.toString()).subscribe((res: MovieResponse) => {
        if (res.Response == 'True') {
          this.movies = res.Search;
          this.total = res.totalResults;
          this.hasMovies = "true";
          console.info(res);
        }else{
          this.hasMovies = "false";
        }
      },
        (error: any) => {
          console.error(error);
        });
    }
  }

  pageChanged($event): void {
    this.p = $event;
    this.hasMovies = "";
    this.httpService.GetMovieList(this.searchString, this.p.toString()).subscribe((res: MovieResponse) => {
      if (res.Response == 'True') {
        this.movies = res.Search;
        this.total = res.totalResults;
        this.hasMovies = "true";
      }else{
        this.hasMovies = "false";
      }
    },
      (error: any) => {
        console.error(error);
      });
  }

  showMovieDetails(movie: Movies): void
  { 
    this.showDetails = true;
    this.httpService.GetMovieDetail(movie.imdbID).subscribe((res: Movies) => {
      console.info(res);
      if(res){
        this.movieDetail = res;
      }
    },  
      (error: any) => {
        console.error(error);
      });
  }
}
